.row {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  left: 20%;
  width: 60%;
  text-align: center;
  align-items: center;
  // background-image: url(../assets/logos/background.png);
  a {
    img {
      width: 25%;
      justify-self: center;
      cursor: pointer;
    }
  }
}
